import React from "react"
import "./Scheme/ContactUsCss.css"
import ColorScheme from "./Scheme/Colors";
class ContactUs extends React.Component {



    render(){
        const btnStyle ={
            backgroundColor:ColorScheme.header.secondary
        }
        return ( 
             <section className="bg-contact bg-section" id="contact">
        <div className="container-fluid">
            <h1 className="container-h1">¡Contactanos!</h1>
            <div className="row ">{/*slideanim */}
                <div className="col-md-6 col-sm-6 contact-left">
                    <div className="left-box" style={{
                        backgroundColor:ColorScheme.header.primary,
                        borderRadius:"30px"
                    }}>
                    <address className="contact">
                            <span className="span-contact">Numero:</span>
                            <br/>
                                332 106 895 3
                            <br/> 
                            <span className="span-contact">Email:</span> 
                            <br/>
                            upch.ids@gmail.com
                            <br/>
                            <span className="span-contact">Dirección:</span>  
                            <br/>
                            Esta en suchiapa
                            <br/>
                            Por la salida, cerca de un puente
                            <br/>
                            Chiapas, Mexico
                        </address>
                        
                    </div>
                </div>

                <div className="col-md-6 col-sm-6 contact-right" >
                            
                            <form noValidate action="mailto:" name="frm" method="post">
                                 <div className="form-group has-feedback">
                                    <label className="sr-only">Nombre(s):</label>
                                    <input type="text" name="name" className="form-control" placeholder="Nombre(s)" required/>
                                    
                                    </div>
                                 <div className="form-group has-feedback">
                                    <label className="sr-only">Apellido(s):</label>
                                    <input type="text" name="surname" className="form-control" placeholder="Apellido(s)" required/>
                                    
                                </div>
                                <div className="form-group has-feedback">
                                    <label className="sr-only">Email:</label>
                                    <input type="email" name="email" className="form-control"  placeholder="Email" required/>
                                    
                                </div>
                                <div className="form-group">
                                    <label className="sr-only" name="comment" htmlFor="comment">:</label>
                                    <textarea className="form-control" rows="5" id="comment" placeholder="Descripción"></textarea>
                                </div>
                                <div className="contact-buttons pull-left">
                                    <input style={btnStyle} type="submit" name="submit"  value="Enviar" />
                                    <input style={btnStyle} type="reset" value="Limpiar" />
                                </div>
                            </form>
                            
                </div>
            </div>
            <div className="row">
                <div id="googleMap"></div>

                <script src="http://maps.googleapis.com/maps/api/js"></script>
                <script>

                </script>
            </div>
        </div>    
    </section> )
    }
}
export default ContactUs;