
import React from "react";

import ColorScheme from "./../Scheme/Colors"
import {Button} from "reactstrap";

class FixedNavBar extends React.Component {

    constructor(props){
        super()
        this.state={
            collapse :true
        }
        this.toggle =  this.toggle.bind(this)
    }
    toggle(){
        this.setState({collapse: !this.state.collapse})
    }
    render(){
        return(
            <React.Fragment>
                <nav style={{ backgroundColor: ColorScheme.header.primary }} className="navbar navbar-expand-md  navbar-dark fixed-top">
                    <a className="navbar-brand" href="#">Software</a>
                    <button onClick={this.toggle} className="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsibleNavbar">
                        <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className={(this.state.collapse ?"collapse ":"") + " navbar-collapse"} id="collapsibleNavbar">
                        <ul className="navbar-nav ml-auto">
                            <li className="nav-item">
                                <a className="nav-link" onClick={this.toggle} href="#about">Acerca de..</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" onClick={this.toggle} href="#menu"> ? </a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link"  onClick={this.toggle} href="#valores">Servicios</a>
                            </li>
                            <li className="nav-item">
                                <a className="nav-link" onClick={this.toggle}  href="#contact">Conctacto</a>
                            </li>
                        </ul>
                    </div>
                </nav>

            </React.Fragment>
        )
    }
}

export default FixedNavBar;